.createVote_content {
    padding-top: 10px;
}
.vote_title {
    border: 1px solid #fff;
    border-bottom: 1px solid #eee;
    padding: 5px 20px;
    width: 100vw;
    height: 52px;
    font-size: 26px;
}
.vote_item {
    border: 1px solid #f0f0f0;
    border-bottom: 1px solid #eee;
    padding: 5px 20px;
    width: 100vw;
    height: 44px;
    font-size: 17px;
}
.vote_ul {
    padding: 0;
    margin: 0;
}

.create_vote_list {
    padding: 5px 20px;
    width: 100vw;
    height: 44px;
    background-color: #fff;
    flex-flow: nowrap row;
    list-style-type: none;
    align-items: center;
    justify-self: start;
    display: flex;
    border-bottom: 1px solid #eee;
    .create_vote_btn {
        // display: flex;
        // justify-content: center;
        // align-items: center;
        width: 20px;
        height: 20px;
        background-color: rgb(255,78,73);
        line-height: 100%;
        text-align: center;
        font-size: 0;
        color: #fff;
        font-size: 0;
        border: none;
        border-radius: 100%;
        span {
            // line-height: 100%;
            vertical-align: middle;
            font-size:17px;

        }
        &:before{
            content: "";
            line-height: 20px;
            display: inline-block;
            vertical-align: middle;
            visibility: hidden; }
    }
    .create_vote_text {
        width: 80vw;
        height: 100%;
        border: none;
        padding: 0 10px;
    }
    .vote_add {
        background-color: rgb(24,107,246);
        margin-right: 10px;
        text-align: center;
        & ~ span {
            color:rgb(24,107,246);
            font-size: 14px;
        }
    }
}
.create_vote_space.create_vote_list {
    margin-top: 44px;
}

.deadline {
    border: none;
    background-color: #fff;
    margin-left: 40px;
}

.create_vote_select.create_vote_list {
    justify-content: space-between;
}

.finish_btn {
    margin-top: 20px;
    width: 291px;
    height: 34px;
    background-color:   rgb(29,109,254);
    border: none;
    border-radius: 4px;
    color: #fff;
}
