
ul {
    padding: 0;
    margin: 0;
}
li {
    list-style: none;
}
.voteInfo_content {
    padding: 0 10%;
    text-align: start;
}
.voteInfo_title {
    display: flex;
    margin-top: 30px;
    justify-content: space-between;
    padding: 0 10%;
    div {
        width: 32px;
        height: 32px;
        border-radius: 999px;
        background-color: rgb(30, 113, 255);
        color: #fff;
        line-height: 32px;
        text-align: center;
        * {
            vertical-align: middle;
        }
    }
}
.voteInfo_desc {
    display: flex;
    // margin-top: 30px;
    // justify-content: space-between;
    padding: 0 10%;
    span {
        color: rgb(30, 113, 255);
    }
}


.option-text {
    width: 120px;
    display: inline-block;
    text-align: left;
}
.option-ratio {
    height: 2px;
    background-color: #276beb;
    margin: 5px 0;
}
.avatars {
    list-style: none;
    margin: 0;
    padding: 0;
}
.avatars > li {
    display: inline-block;
}
.avatar {
    width:32px;
    height: 32px;
    border-radius: 100%;
}

// checkbox样式
.checkbox_item   {
    background-color: #fff;
    color: #276beb;
    border: none;
}

// 截止日期提示样式
.deadline_tip {
    margin-top: 20px;
    color: #bbb;
    font-size: 14px;
}
