.create_content {
    display: flex;
    flex-direction: column;
    padding: 6px;
    .create_item {
        width: 100%;
        height: 240px;
        display: flex;
        flex-direction: column;
        align-items:center;
        justify-content: center;
        // flex: 1;
        // background-color: lime;
        .img {
            display: block;
        }
        .create_item_btn {
            width: 212px;
            height: 32px;
            color: #fff;
            border-radius: 3px;
            text-align: center;
            background-color: rgb(30, 113, 255);
        }
        a{
            text-decoration:none;
            color:#fff;
            line-height: 33px;
        }
    }
}
