.create_vote_list.myvote_list {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
}
.vote_link {
    text-decoration: none;
    font-size: 17px;
    display: block;
    color: #777;
}
.delete_vote {
    display: block;
    border: none;
    background-color: rgb(24,107,246);
    color: #fff;
    border-radius: 3px;
}
