.login_nav {
    margin-top: 40px;
    width: 100vw;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
}


.login_form {
    width: 100vw;
    // display: flex;
}

.login_form_item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    margin: 20px 10%;
    background-color: rgb(235,244,255);
    border-radius: 8px;
}

.login_form_input {
    width: 58vw;
    flex: 1;
    border: none;
    height: 33px;
    background-color: rgba($color: #000000, $alpha: 0);
    &:hover {
        outline: none;
    }
}

.login_form_item.login_tip {
    background-color: transparent;
    display: block;
    text-align: left;
    font-size: 12px;
    color: #999;
}
