.title {
    display: flex;
    flex-flow: nowrap row;
    align-items: center;
    padding: 10px;
    background-color: #f5f5f5;
}
.body {
    width: 100vw;
    height: 100vh;
    position: relative;
}
.navbar {
    padding: 12px;
    width: 100vw;
    position: absolute;
    bottom: 0;
    margin-top: 80px;
    border-top: 1px solid #ccc;
    display: flex;
    .navbar_item {
        flex: 1;
        .iconfont  {
            font-size: 22px;
        }
    }

    a{
        text-decoration:none;
        color: #ccc;
    }
    .active {
        color:rgb(30, 113, 255);

    }
}
